/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import {
	RichText,
	InnerBlocks,
	PanelColorSettings,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, CheckboxControl, BaseControl } from '@wordpress/components';
import { useCallback } from '@wordpress/element';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import deprecated from './deprecated';
import { LsIcon, LsIconPicker, ArkbProOnly } from '@components';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * Register Block
 */
const blockName = 'ark-block-timeline';
registerBlockType(metadata.name, {
	title: __('Timeline item', 'arkhe-blocks'),
	icon: blockIcon,
	edit: ({ attributes, setAttributes }) => {
		const { title, label, isFill, isEmphasis, icon, color, hideContent } = attributes;

		// シェイプクラス
		const shapeClass = classnames(`${blockName}__shape`, {
			'-is-fill': isFill,
			'-is-emphasis': isEmphasis,
			'-has-icon': !!icon,
		});
		const shapeStyle = color ? { color } : null;

		// アイコン選択時の処理
		const setIcon = useCallback(
			(val) => {
				if (!IS_PRO) return;
				setAttributes({ icon: val });
			},
			[setAttributes]
		);

		// Props
		const blockProps = useBlockProps({
			className: `${blockName}__item`,
		});
		const innerBlocksProps = useInnerBlocksProps(
			{
				className: `${blockName}__body ark-keep-mt--s`,
			},
			{
				template: [['core/paragraph']],
				templateLock: false,
			}
		);

		return (
			<>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')}>
						<CheckboxControl
							label={__('Hide content', 'arkhe-blocks')}
							checked={hideContent}
							onChange={(val) => setAttributes({ hideContent: val })}
						/>
						<CheckboxControl
							label={__('Fill the shape', 'arkhe-blocks')}
							checked={isFill}
							onChange={(val) => setAttributes({ isFill: val })}
						/>
						<CheckboxControl
							label={__('Emphasize the shape', 'arkhe-blocks')}
							checked={isEmphasis}
							onChange={(val) => setAttributes({ isEmphasis: val })}
						/>
					</PanelBody>
					<PanelColorSettings
						title={__('Color settings', 'arkhe-blocks')}
						initialOpen={true}
						colorSettings={[
							{
								value: color,
								label: __('Color', 'arkhe-blocks'),
								onChange: (value) => {
									setAttributes({ color: value });
								},
							},
						]}
					/>
					<PanelBody title={__('Icon settings', 'arkhe-blocks')}>
						<ArkbProOnly>
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Select Icon', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<LsIconPicker value={icon} onChange={setIcon} clearable={true} />
							</BaseControl>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						<span className={shapeClass} style={shapeStyle}>
							{icon && <LsIcon icon={icon} className={`${blockName}__icon`} />}
						</span>
						<RichText
							placeholder='2020.01.01'
							className={`${blockName}__label`}
							tagName='span'
							value={label}
							onChange={(val) => setAttributes({ label: val })}
						/>
					</div>
					<RichText
						placeholder={__('Enter text', 'arkhe-blocks') + '...'}
						className={`${blockName}__title is-empty-${RichText.isEmpty(title)}`}
						tagName='div'
						value={title}
						onChange={(val) => setAttributes({ title: val })}
					/>
					{!hideContent && <div {...innerBlocksProps} />}
				</div>
			</>
		);
	},
	save: ({ attributes }) => {
		const { title, label, isFill, isEmphasis, icon, color, hideContent } = attributes;

		const shapeClass = classnames(`${blockName}__shape`, {
			'-is-fill': isFill,
			'-is-emphasis': isEmphasis,
			'-has-icon': !!icon,
		});
		const shapeStyle = color ? { color } : null;

		const blockProps = useBlockProps.save({
			className: `${blockName}__item`,
		});

		return (
			<div {...blockProps}>
				<div className={`${blockName}__head`}>
					<span className={shapeClass} style={shapeStyle}>
						{icon && <LsIcon icon={icon} className={`${blockName}__icon`} />}
					</span>
					<span className={`${blockName}__label`}>
						<RichText.Content value={label} />
					</span>
				</div>
				{!RichText.isEmpty(title) && (
					<div className={`${blockName}__title`}>
						<RichText.Content value={title} />
					</div>
				)}
				{!hideContent && (
					<div className={`${blockName}__body ark-keep-mt--s`}>
						<InnerBlocks.Content />
					</div>
				)}
			</div>
		);
	},
	deprecated,
});
