/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { LsIcon } from '@components';
import { migrateIconName } from '@helper/migrate';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * deprecated
 */
const blockName = 'ark-block-timeline';
export default [
	{
		// 「iconのaira周り調整 & role="presentation" 削除」 の前
		supports: { anchor: true, className: false, reusable: false },
		attributes: {
			title: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-timeline__title',
			},
			label: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-timeline__label',
			},
			isFill: {
				type: 'boolean',
				default: false,
			},
			isEmphasis: {
				type: 'boolean',
				default: false,
			},
			hideContent: {
				type: 'boolean',
				default: false,
			},
			icon: {
				type: 'string',
				default: '',
			},
			color: {
				type: 'string',
				default: '',
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-timeline__shape.-has-icon',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
				console.log('Timelne icon migrated:', newAttrs.icon);
			}

			return {
				...attributes,
				...newAttrs,
			};
		},
		save: ({ attributes }) => {
			const { title, label, isFill, isEmphasis, icon, color, hideContent, iconV1 } =
				attributes;

			const oldSvg = iconV1 || '';
			// oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意

			let theIcon = null;
			if (oldSvg) {
				// console.log('Notice old icon:', oldSvg);
				theIcon = <RawHTML>{oldSvg}</RawHTML>;
			} else if (icon) {
				theIcon = <LsIcon icon={icon} className={`${blockName}__icon`} />;
			}

			const shapeClass = classnames(`${blockName}__shape`, {
				'-is-fill': isFill,
				'-is-emphasis': isEmphasis,
				'-has-icon': !!icon,
			});
			const shapeStyle = color ? { color } : null;

			const blockProps = useBlockProps.save({
				className: `${blockName}__item`,
			});

			return (
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						<span className={shapeClass} style={shapeStyle}>
							{theIcon}
						</span>
						<span className={`${blockName}__label`}>
							<RichText.Content value={label} />
						</span>
					</div>
					{!RichText.isEmpty(title) && (
						<div className={`${blockName}__title`}>
							<RichText.Content value={title} />
						</div>
					)}
					{!hideContent && (
						<div className={`${blockName}__body ark-keep-mt--s`}>
							<InnerBlocks.Content />
						</div>
					)}
				</div>
			);
		},
	},
];
